<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Jadwal Ujian</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-content class="ion-padding">
        <ion-row>
          <ion-col v-for="jadwal in filteredJadwalUjian" :key="jadwal.id">
            <ion-card @click="checkExamStatus(jadwal)">
              <ion-card-header>
                <ion-card-title
                  >Ujian Mata Pelajaran {{ jadwal.subjectName }}</ion-card-title
                >
              </ion-card-header>
              <ion-card-content>
                <p>Kelas: {{ jadwal.className }}</p>
                <p>Durasi: {{ jadwal.durasi }} Menit</p>
                <p>Tanggal Ujian: {{ formatDate(jadwal.tanggalUjian) }}</p>
              </ion-card-content>
            </ion-card>
          </ion-col>
        </ion-row>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonToast,
  IonBackButton,
  IonRow,
  IonCol,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
} from "@ionic/vue";
import { ref, onMounted, computed } from "vue";
import {
  getFirestore,
  collection,
  getDocs,
  doc,
  getDoc,
  query,
  where,
} from "firebase/firestore";
import { getAuth } from "firebase/auth";
import { useRouter } from "vue-router";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const router = useRouter();
const jadwalUjian = ref([]);
const userClass = ref("");
const examResults = ref([]);
const classes = ref([]);
const subjects = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchUserClass = async () => {
  const user = auth.currentUser;
  if (user) {
    const userDoc = await getDoc(doc(db, "users", user.uid));
    if (userDoc.exists()) {
      userClass.value = userDoc.data().kelas; // Mengambil nilai dari kolom 'kelas'
    }
  }
};

const fetchClasses = async () => {
  const querySnapshot = await getDocs(collection(db, "kelas"));
  classes.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const fetchSubjects = async () => {
  const querySnapshot = await getDocs(collection(db, "mataPelajaran"));
  subjects.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const fetchJadwalUjian = async () => {
  const querySnapshot = await getDocs(collection(db, "jadwal"));
  jadwalUjian.value = querySnapshot.docs.map((doc) => {
    const data = doc.data();
    const classData = classes.value.find((kelas) => kelas.id === data.kelas);
    const subjectData = subjects.value.find(
      (subject) => subject.id === data.mataPelajaran
    );
    return {
      id: doc.id,
      ...data,
      className: classData ? classData.name : "N/A",
      subjectName: subjectData ? subjectData.name : "N/A",
    };
  });
};

const fetchExamResults = async () => {
  const user = auth.currentUser;
  if (user) {
    const q = query(
      collection(db, "examResults"),
      where("userId", "==", user.uid)
    );
    const querySnapshot = await getDocs(q);
    examResults.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
  }
};

const checkExamStatus = (jadwal) => {
  if (jadwal.aktif) {
    goToUjian(jadwal.id);
  } else {
    showToast("Ujian belum dimulai.", "warning");
  }
};

const goToUjian = (jadwalId) => {
  if (hasTakenExam(jadwalId)) {
    showToast("Anda sudah melakukan ujian ini.", "danger");
    router.push({ name: "lihathasilujianUser" });
  } else {
    router.push({ name: "halamanujianUser", params: { jadwalId } });
  }
};

const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  let date;
  if (timestamp.seconds) {
    date = new Date(timestamp.seconds * 1000);
  } else if (typeof timestamp === "string" || timestamp instanceof Date) {
    date = new Date(timestamp);
  } else {
    return "Invalid Date";
  }
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const hasTakenExam = (jadwalId) => {
  return examResults.value.some((result) => result.jadwalId === jadwalId);
};

const filteredJadwalUjian = computed(() => {
  return jadwalUjian.value.filter((jadwal) => jadwal.kelas === userClass.value);
});

onMounted(async () => {
  await fetchClasses(); // Fetch classes first
  await fetchSubjects(); // Fetch subjects first
  await fetchUserClass();
  await fetchJadwalUjian();
  await fetchExamResults();
});

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};
</script>

<style scoped>
